.play {
    background-color: #000000cc;
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .play .content{
    height: 90%;
    width: 80%;
    padding: 3px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  .play .close-btn{
    font-size: 1em;
    padding: 5px;
    background: #000A;
    color: white;
    margin-top: 5px;
    position: absolute;
    right: 0;
    top:0;
    width: 50px;
    margin-right: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 25px;

  }