.header {
  position: relative;
  width: 1152px;
  text-align: center;
  height: 95px;
  margin-top: 20px;
}
.header .logo {
  margin-left: 10px;
  width: 85px;  
  position: relative;
  display: flex;
  border: 3px solid rgb(250, 248, 248);
  border-radius: 100px;
  height: 84px;

  
}
.header .AppBar{
  position: absolute;
  right: 40px;
  top: 0;
  min-width: 1008px;
  background-image: linear-gradient( 150deg, #Af3999 30%, #265FDF 70%, #369FFF 100%);
  text-align: center;
  border: 1px solid white;
  box-sizing: border-box;
  border-radius: 25px;
  color: #000;
  height: 95px;
  display: flex;
  flex:content;
}
.header .AppBar .cart{
  position: absolute;
  right: 30px;
  top: 22px;
  display: flex;
  padding: 6px 12px;
  margin-right: 5px;
  flex-direction: column;
  display: inline-block;
  background-image: linear-gradient( 150deg, #4e0741 30%, #1c43a0 70%, #123f69 100%);
  color: white;
  border-radius: 25px;
  cursor: pointer;
  border-color: white;
  border-width: 1px;
}
.header .SiteName {
  position: relative;
  left: 20px;
  top: -78px;
  padding-bottom: 80px;
  font-size: 72px;
  font-weight: bolder;
  font-family: 'Tangerine', cursive,bold;
  color: white;
  text-shadow: 2px 3px rgb(70, 57, 57);
  display: flex;
}


.header .Login {
  position: absolute;
  right: 90px;
  top: 13px;
  display: flex;
}
.header .Login button {
  display: block;
  padding: 6px 12px;
  margin-right: 5px;
  flex-direction: column;
  display: inline-block;
  background-image: linear-gradient( 150deg, #4e0741 30%, #1c43a0 70%, #123f69 100%);
  color: white;
  border-radius: 25px;
  cursor: pointer;
  border-color: white;
  border-width: 1px;
}
.header .Login .selectedTab-btn {
  
  background-image: linear-gradient( 150deg, #f1eef1 30%, #d3d8e4 70%, #cfdce7 100%);
  color: darkblue;
  border-color: white;
}
.header .AppBar nav {
  position: absolute;
  right: 210px;
  top: 30px;
  display: flex;
}
.header .AppBar nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: right;
  display: flex;
}
.header .AppBar nav button {
  display: block;
  padding: 6px 12px;
  margin-right: 5px;
  flex-direction: column;
  display: inline-block;
  background-image: linear-gradient( 150deg, #4e0741 30%, #1c43a0 70%, #123f69 100%);
  color: white;
  border-radius: 25px;
  cursor: pointer;
  border-color: white;
  border-width: 1px;
}
.header .AppBar nav .selectedTab-btn {
  
  background-image: linear-gradient( 150deg, #f1eef1 30%, #d3d8e4 70%, #cfdce7 100%);
  color: darkblue;
  border-color: white;
}