.login {
    background-color: rgb(0 0 0 / 54%);
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
  }
  .login .form{
    width: 50%;
    height:  50%;
    
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    display: flex;
    border-radius: 25px;
    border: 5px solid #fff;
    box-shadow: -2px -3px #000;
    position: fixed;
  }
  .login .box .close-btn{
    font-size: -1em;
    padding: 5px;
    background: #000A;
    color: white;
    margin-top: 5px;
    position: absolute;
    right: -5px;
    top:-5px;
    max-width: 50px;
    margin-right: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 25px;

  }
  .login .box {
    position: absolute;
    padding: 2%;
    display: grid;
    background-image: linear-gradient( 150deg, #a05692 30%, #4c6397 70%, #517aa0 100%);
  }
  .login .box button {
    display: block;
    margin-top: 12px;
    margin-right: 5px;
    flex-direction: column;
    display: inline-block;
    background-image: linear-gradient( 150deg, #4e0741 30%, #1c43a0 70%, #123f69 100%);
    color: white;
    border-radius: 25px;
    cursor: pointer;
    border-color: white;
    border-width: 1px;
    text-align: left;
  }
  .login .box input {
    margin  : 2px;
    display: block;
    flex-direction: column;
    display: inline-block;
    background-color:white;
    border-radius: 5px;
    text-align: center;
  }
  .login .box a {
    margin-bottom: 12px;
    display: block;
    flex-direction: column;
    display: inline-block;
    background-color: linear-gradient( 150deg, #a05692 30%, #4c6397 70%, #517aa0 100%);
    border-radius: 5px;
    text-decoration: none;
    color: rgb(220, 235, 235);
    border: 0.5px solid rgb(8, 0, 0);
    
  }